@import url(https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta1/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-giJF6kkoqNQ00vy+HMDP7azOuL0xtbfIcaT9wjKHr8RbDVddVHyTfAAsrekwKmP1" crossorigin="anonymous);
@import url(https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta1/dist/js/bootstrap.bundle.min.js" integrity="sha384-ygbV9kiqUc6oa4msXn9868pTtWMgiQaeYH7/t7LECLbyPA2x65Kgf80OJFdroafW" crossorigin="anonymous);

*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Serif', sans-serif;
  background-color: #fff;
}
.navbar
{
  background-color:#fff!important;
}
.navbar-brand
{
font-size: 1.9rem !important;
color:#4c50ac !important;
}

.navbar a
{
font-size: 1.3rem !important;
text-transform: capitalize;
}
.menu_active
{
  /* font-weight: bold; */
  border-bottom:1px solid #565387 ;
}
.navbar a:hover
{
  color:#4c50ac !important;
}
.navbar-nav
{
margin-left: auto;
background: cyan;

}
@media(max-width:991px)
{
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}

#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px; 
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color:black;
}
#header .brand-name
{
  color: #4c50ac;
}

#header h2{
  color: #484848;
  font-size: 24px;
}
#header .btn-get-started
{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #4c50ac;
  border: 2px solid #4c50ac;
  text-decoration: none;

}

#header .btn-get-started:hover{
  background:#4c50ac;
  color: #fff;
}
